import React, { Component } from 'react';

import Row from 'react-bootstrap/lib/Row';
import Col from 'react-bootstrap/lib/Col';
import styled from 'styled-components';

import Slider from './Slider';

const Repair = styled.div`
	color: #ffffff;
	font-size: 40px;
	font-weight: 700;
	line-height: 50px;
	span {
		font-size: 24px;
		line-height: 30px;
		display: block;
	}
`
const LightText = styled.div`
	color: #ffffff;
	font-size: 18px;
	font-weight: 300;
	line-height: 28px;
	padding-top: 54px;
`

const CallButton = styled.button`
	width: 247px;
	height: 67px;
	background-color: #ffa14d;
	border-radius: 34px;
	color: white;
	font-size: 18px;
	font-weight: 600;
	margin-top: 32px;
	border: none;
`

class Main extends React.Component {
	render(){
		return (
				<Row>
					<Col lg={5}>
						<Repair>
							 
							<span>iphone  35    1 </span>
						</Repair>
						<LightText>
						      ,
						   ,  1000 ,
						   !
						</LightText>
						<CallButton>
							 !
						</CallButton>
					</Col>
					<Col lg={6} lgOffset={1}>
						<Slider></Slider>
					</Col>
				</Row>
			)
	}
}

export default Main


