import React, { Component } from 'react';

import Row from 'react-bootstrap/lib/Row';
import Col from 'react-bootstrap/lib/Col';
import styled from 'styled-components';

const Repair = styled.div`
	font-size: 18px;
	font-weight: 300;
	line-height: 22px;
`

const WorkTime = styled.div`
color: #2a2a2a;
font-size: 12px;
font-weight: 300;
line-height: 24px;

span {
	display: block;
	color: #464646;
	font-size: 21px;
	font-weight: 700;
}
`
class Address extends React.Component {
	render() {
		return (
				<span>{this.props.addr}</span>
			)
	}
}


const Calls = styled.div`
color: #2a2a2a;
font-size: 12px;
font-weight: 300;
line-height: 24px;

span {
	display: block;
	color: #464646;
	font-size: 21px;
	font-weight: 700;
}
`
class TelNumber extends React.Component {
	render() {
		return (
				<span>{this.props.tel}</span>
			)
	}
}

const CallButton = styled.button`
	width: 176px;
	height: 48px;
	background-color: #3fc7db;
	border-radius: 34px;
	color: white;
	font-size: 14px;
	font-weight: 400;
	line-height: 24px;
	border: none;
`


class Menu extends React.Component {
	render() {
		return (
				<Row>
					<Col lg={3}>
						<Repair>
							       
						</Repair>
					</Col>
					<Col lg={3} lgOffset={1}>
						<WorkTime>
							-  10  20, ,  11  18
							<Address addr=", 208" />
						</WorkTime>
					</Col>
					<Col lg={3}>
						<Calls>
							  24 
							
							<TelNumber tel="8 (846) 922 55 44 " />
						</Calls>
					</Col>
					<Col lg={2}>
						<CallButton>
							 !
						</CallButton>
					</Col>
				</Row>
			)
	}
}

export default Menu;


